import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { Modal, Text, View } from 'react-native';
import { IconButton, RadioButton, Surface, Switch } from 'react-native-paper';
import GameContext from '../../Context/GameContext/GameContext';
import BaseCss from '../../Styles/BaseCss';
import defaultState from "../../_shared/constants/defaultState";

// Allows players to confirm quit.
const Settings = ({ modalVisible, setModalVisible }) => {
  const { dice, settings, dispatch } = useContext(GameContext);

  const toggleConfirm = () => {
    dispatch({
      type: 'settings',
      payload: {
        ...settings,
        confirmMoves: !settings.confirmMoves,
      },
    });
  };

  const toggleUndo = () => {
    dispatch({
      type: 'settings',
      payload: {
        ...settings,
        allowUndo: !settings.allowUndo,
      },
    });
  };

  const setDifficulty = (value) => {
    dispatch({
      type: 'settings',
      payload: {
        ...settings,
        difficultyLevel: value,
      },
    });
  };

  const setTotals = (value) => {
    dispatch({
      type: 'settings',
      payload: {
        ...settings,
        showTotals: value,
      },
    });
  };

  const setBase = (value) => {
    const limit = parseInt(value, 10);
    const options = [];
    for (let i = 1; i <= limit; i += 1) {
      options.push(i);
    }
    dispatch({
      type: 'dice',
      payload: {
        ...dice,
        base: limit,
        options,
        blocker: limit + 1,
      },
    });
  };

  const prepLevel = (level) => {
    let text = '';
    switch (level) {
      case 'easy':
        text = 'Easy';
        break;
      case 'centered':
        text = 'Medium';
        break;
      case 'random':
        text = 'Hard';
        break;
      default:
        break;
    }
    return ` ${text} `;
  };

  // @todo Make this pretty.
  return (
    <Modal
      onRequestClose={() => { return setModalVisible(null); }}
      visible={modalVisible}
      transparent
      testID="settingsModal"
    >
      <View style={BaseCss.modalSettingsContainer}>
        <Surface style={BaseCss.settingsModal} elevation={5}>
          <View style={BaseCss.settingsHeader}>
            <View style={BaseCss.settingsClose} />
            <View style={BaseCss.settingsHeaderText}>
              <Text style={BaseCss.settingsText} testID="settingsHeader">
                GAME SETTINGS
              </Text>
            </View>
            <View style={BaseCss.settingsClose}>
              <IconButton
                icon="close-box-outline"
                iconColor="#069"
                size={28}
                onPress={() => {
                  setModalVisible(null);
                }}
                style={BaseCss.settingsButton}
                testId="settingsClose"
              />
            </View>
          </View>
          <Text style={BaseCss.settingsText} testID="settingsHeader">
            Confirm Moves
          </Text>
          <Switch
            value={settings.confirmMoves}
            onValueChange={toggleConfirm}
          />
          <Text style={BaseCss.settingsText} testID="settingsHeader">
            Allow Undo
          </Text>
          <Switch
            value={settings.allowUndo}
            onValueChange={toggleUndo}
          />
          <Text style={BaseCss.settingsText} testID="settingsHeader">
            Show Totals
          </Text>
          <RadioButton.Group
            onValueChange={(newValue) => {
              setTotals(newValue);
            }}
            value={settings.showTotals}
          >
            <View>
              <Text>Off</Text>
              <RadioButton value="off" />
            </View>
            <View>
              <Text>Sums</Text>
              <RadioButton value="totals" />
            </View>
            <View>
              <Text>Remainders</Text>
              <RadioButton value="remainders" />
            </View>
          </RadioButton.Group>
          <Text style={BaseCss.settingsText} testID="settingsHeader">
          Difficulty Level
          </Text>
          {!settings.inProgress && (
            <RadioButton.Group
              onValueChange={(newValue) => {
                setDifficulty(newValue);
              }}
              value={settings.difficultyLevel}
            >
              <View>
                <Text>Easy</Text>
                <RadioButton value="easy" />
              </View>
              <View>
                <Text>Medium</Text>
                <RadioButton value="centered" />
              </View>
              <View>
                <Text>Hard</Text>
                <RadioButton value="random" />
              </View>
            </RadioButton.Group>
          )}
          {settings.inProgress && (
            <View>
              <Text>
                Difficulty is set to
                {prepLevel(settings.difficultyLevel)}
                and cannot be changed until a new game is started.
              </Text>
            </View>
          )}
          <Text style={BaseCss.settingsText} testID="settingsHeader">
            Base Value
          </Text>
          {!settings.inProgress && (
            <RadioButton.Group
              onValueChange={(newValue) => {
                setBase(newValue);
              }}
              value={dice.base.toString()}
            >
              <View>
                <Text>6</Text>
                <RadioButton value="6" />
              </View>
              <View>
                <Text>8</Text>
                <RadioButton value="8" />
              </View>
              <View>
                <Text>10</Text>
                <RadioButton value="10" />
              </View>
            </RadioButton.Group>
          )}
          {settings.inProgress && (
            <View>
              <Text>
                Base value is set to
                {` ${dice.base} `}
                and cannot be changed until a new game is started.
              </Text>
            </View>
          )}
        </Surface>
      </View>
    </Modal>
  );
};

Settings.propTypes = {
  modalVisible: PropTypes.bool,
  setModalVisible: PropTypes.func,
};
Settings.defaultProps = {
  modalVisible: false,
  setModalVisible: {},
};

export default Settings;
